.settings{
    display: flex;
}

.settingWrapper{
    flex: 9;
    padding: 20px;
}

.settingTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingUpdatetitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
}

.settingDeletetitle
{
    color: red;
    font-size: 12px;
    cursor: pointer;
}

.settingForm{
    display: flex;
    flex-direction: column;
}

.settingPP{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.settingPP > img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
}
.settingPPicon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: lightcoral;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
    color: white;
}
.settingForm>label{
    font-style: 20px;
    margin-top: 20px;
}
.settingForm>input{
    color: gray;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgrey;
}

.settingSubmit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    
}