.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color:  gainsboro;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;


}
.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle{
    margin: 10px;
    padding: 5px;
    width: 100%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-size: 12px;
    color: #222;
    font-weight: 500;
    text-align: center;
    line-height: 20px;
}

.sidebarList{
    list-style: none;
    margin-bottom: 30px;
}
.fa-hospital{
    margin-right: 15px;
}
.sidebarListItems{
    margin-top: 10px;
    cursor: pointer;
}

.sidebarSocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebarIcons{
    font-size: 16px;
    margin-left: 10px;
    cursor: pointer;

}